@import url("https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,400;0,500;0,600;0,700;0,800;1,900&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-nunito: Nunito;

  /* font sizes */
  --font-size-lg: 18px;
  --font-size-11xl: 30px;
  --font-size-base: 16px;
  --font-size-mini: 15px;
  --font-size-xs: 12px;
  --font-size-31xl: 50px;
  --font-size-21xl: 40px;
  --font-size-6xl: 25px;
  --font-size-lgi: 19px;
  --font-size-61xl: 80px;
  --font-size-51xl: 70px;
  --font-size-xl: 20px;
  --font-size-76xl: 95px;

  /* Colors */
  --color-white: #fff;
  --color-olivedrab: #548c2f;
  --color-mintcream: #f4f8f2;
  --color-gray-100: rgba(255, 255, 255, 0.9);
  --color-gray-200: rgba(255, 255, 255, 0.6);
  --color-gray-300: rgba(255, 255, 255, 0.3);
  --color-whitesmoke-100: rgba(236, 236, 236, 0.95);

  /* border radiuses */
  --br-6xl: 25px;
  --br-21xl: 40px;
  --br-2xl: 21px;
  --br-139xl: 158px;
}
